@keyframes fadeInUp {
  0% {
      opacity: 0;
      -webkit-transform: translate3d(0,100%,0);
      -ms-transform: translate3d(0,100%,0);
      transform: translate3d(0,100%,0)
  }

  100% {
      opacity: 1;
      -webkit-transform: none;
      -ms-transform: none;
      transform: none
  }
}

@keyframes fadeIn {
  0% {
      opacity: 0
  }

  100% {
      opacity: 1
  }
}

@keyframes zoomIn {
  0% {
      opacity: 0;
      -webkit-transform: scale3d(.3,.3,.3);
      -ms-transform: scale3d(.3,.3,.3);
      transform: scale3d(.3,.3,.3)
  }

  50% {
      opacity: 1
  }
}


