.hero-image {
  width: 100vw;
  height: 70vh;
  min-height: 500px;
  
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;

  background-position: center;
  background-size: cover;

  &:before {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: .7;
    background-image: url('../img/bg-dot.png');
  }


}

.hero-title {
  position: relative;
  z-index: 3;
  opacity: 0;
  animation: fadeIn 1.5s ease 1 forwards;

  & svg {
    width: 1.5em;

    &.logo-3 {
      width: 3.25em;
    }
  }
}

.full-width {
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
}


